import React from "react";

import { NavLink } from "react-router-dom";

import "./dist/nav.css"

export function NavBar(){
    return(
        <div className="nav_bar">
            <img className="logo" src="./img/a_logo.svg" alt="" />
            <NavLink className="links" activeClassName ="active" to="/"> </NavLink>
            <NavLink className="links" activeClassName ="active" to="/products"> </NavLink>
            <NavLink className="links" activeClassName ="active" to='/catalog'></NavLink>
            <div className="links"> </div>
            <div className="links"> </div>
        </div>
    )
}